import './Tlogin.css';
import { useEffect, useState } from 'react';
import Axios from 'axios';
import './Tlogin.css';
function TeacherStudent() {
    (function () {
        const login_id = (localStorage.getItem('login_id'));
        const ACcesstoken = (localStorage.getItem('access-token'));
        Axios.post('http://localhost:3001/api/teacher/acc', {
            login_id: login_id,
            token: ACcesstoken
        }).then((res) => {
            if (res.data == 'error') {
                alert(" ! ")
                window.location.href = '/teacher';
            } else {
                console.log(" ")
            }
        });
    })();
    
    const [Name, setName] = useState('');
    const [Grade, setGrade] = useState('');
    const [Rate, setRate] = useState('');
    const [Time, setTime] = useState('');
    const [Teacher, setTeacher] = useState('');
    const [Birthday, setBirthday] = useState('');
    const [Week, setWeek] = useState('');
    const [sclass, setsClass] = useState('');
 

    const submitLogin = () => {
        Axios.post('http://localhost:3001/api/teacher/student', {
            Name: Name,
            Grade: Grade,
            Rate: Rate,
            Teacher: Teacher,
            Birthday: Birthday,
            Class:sclass

        }).then((res) => {
            if(res.data == "err"){
                alert("?    ..  !");
            }else{
                alert(" !");
                window.location.href = '/teacher/mypage'
            }
            
        }).catch(()=>{alert('..  ..?')});
    };
    return (
        <div>
            <head>
                <meta charSet="UTF-8"></meta>
                <script src="jquery-3.4.1.js"></script>
            </head>
            <div class="body1">
                <section class="login-form">
                    <div className='flex justify-center items-center'>
                        <img alt='logoimg' src="../img/jongrologo.png" className='w-32 items-center'></img>
                    </div>
                    <from action="">
                        <div class="int-area">
                            <input type="text" name="id" id="id"
                                autoComplete='off' required onChange={(e) => { setName(e.target.value) }}></input>
                            <label for='id'> </label>
                        </div>
                        <div class="int-area">
                            <input type="text" name="pw" id="pw"
                                autoComplete='off' required onChange={(e) => { setGrade(e.target.value) }} />
                            <label for='pw'> </label>
                        </div>
                        <div class="int-area">
                            <input type="text" name="pw" id="pw"
                                autoComplete='off' required onChange={(e) => { setRate(e.target.value) }} />
                            <label for='pw'>  ?</label>
                        </div>
                        <div class="int-area">
                            <input type="text" name="pw2" id="pw2"
                                autoComplete='off' required onChange={(e) => { setTeacher(e.target.value) }} />
                            <label for='pw'>  ?</label>
                        </div>
                        <div class="int-area">
                            <input type="text" name="pw1" id="pw1"
                                autoComplete='off' required onChange={(e) => { setsClass(e.target.value) }} />
                            <label for='pw1'>  ?</label>
                        </div>
                        <div class="int-area">
                            <input type="text" name="pw2" id="pw2"
                                autoComplete='off' required onChange={(e) => { setBirthday(e.target.value) }} />
                            <label for='pw'> </label>
                        </div>
                        <div class="btn-area">
                            <button onClick={submitLogin}>!</button>
                        </div>
                    </from>
                </section>
            </div>
        </div>
    )

}
export default TeacherStudent;