import Header from "./Header";
import Nav from "./Nav";
import Axios from "axios";
import { useState } from "react";
function Mainpage() {

    (function () {
        const login_id = (localStorage.getItem('login_id'));
        const ACcesstoken = (localStorage.getItem('access-token'));
        Axios.post('http://localhost:3001/api/student/acc', {
            login_id: login_id,
            token: ACcesstoken
        }).then((res) => {
            if (res.data == null) {
                alert("  !")
                window.location.href = '/m'
            }
        });
        Axios.post('http://localhost:3001/api/student/homework/acc', {
            login_id: login_id
        }).then((res) => {
            console.log(res.data)
            if (res.data == 'success') {
                window.localStorage.setItem('homework', "");
            } else{
                window.localStorage.setItem('homework','');
            }
        });
        
    })();

    const login_id = (localStorage.getItem('login_id'));
    setTimeout(function(){var homework = (localStorage.getItem('homework'))},10);
    return (
        <div className="h-screen">
            <div alt="header" className="flex h-10 flex-col fixed w-full">
                <Header />
                <div alt="" className="pb-2 pt-16">
                    <div className="text-slate-600 font-semibold"> </div>
                    <div className="text-slate-700 font-thin text-xs"> *   .</div>
                </div>
            </div>
            <div alt="content" className="px-1 pt-28">
                <a alt=" " className="flex text-sm justify-between px-1 bg-blue-900 text-white rounded-lg h-20 pt-2 flex-col" href="/student/homework">
                    <div className="flex justify-between">
                        <div></div>
                        <div className="flex flex-col">
                            <div>{localStorage.getItem('homework')}</div>
                        </div>
                    </div>
                    <div className="flex justify-between pr-24">
                        <div>{login_id} </div>
                        <div>  A</div>
                    </div>
                </a>
            </div>
            <Nav />
        </div>
    )
}

export default Mainpage;