import './login.css';
import { useEffect, useState } from 'react';
import Axios from 'axios';
function StudentLogin() {
    (function () {
        const login_id = (localStorage.getItem('login_id'));
        const ACcesstoken =(localStorage.getItem('access-token'));
        Axios.post('http://localhost:3001/api/student/acc', {
                login_id: login_id,
                token: ACcesstoken
            }).then((res) => {
                if(res.data == 'success'){
                    alert("  !")
                    window.location.href='/student/Mypage'
                }
            });
    })();
    
    const [inputID, setInputId] = useState('');
    const [inputPW, setInputPw] = useState('');
    const [accesstoken, setAccessToken] = useState('');

    const submitLogin = () => {
        Axios.post('http://localhost:3001/api/login/student', {
            inputID: inputID,
            inputPW: inputPW
        }).then((res) => {
            if(res.data == "error"){
                alert(" !!");
            }else{
                alert(" !");
                setAccessToken(res.data);
                localStorage.setItem('login_id' , `${inputID}`);
                localStorage.setItem('access-token' , `${res.data}`);
                window.location.href = '/student/Mypage';
            }
            
        }).catch(()=>{alert('..  ..?')});
        

    };
    const submitwhere = () => {
        alert(" !")
    }
    return (
        <div>
            <head>
                <meta charSet="UTF-8"></meta>
                <script src="jquery-3.4.1.js"></script>
            </head>
            <div class="body1">
                <section class="login-form">
                    <a className='flex justify-center' href='/'>
                        <img alt='logoimg' src="./img/jongrologo.png" className='w-32 items-center'></img>
                    </a>
                    <from action="">
                        <div class="int-area">
                            <input type="text" name="id" id="id"
                                autoComplete='off' required onChange={(e) => { setInputId(e.target.value) }}></input>
                            <label for='id'> !</label>
                        </div>
                        <div class="int-area">
                            <input type="password" name="pw" id="pw"
                                autoComplete='off' required onChange={(e) => { setInputPw(e.target.value) }} />
                            <label for='pw'> !</label>
                        </div>
                        <div class="btn-area">
                            <button onClick={submitLogin}></button>
                        </div>
                    </from>
                    <div class="caption">
                        <button className='hover:text-slate-500 text-gray-400' onClick={submitwhere}> ?</button>
                    </div>
                </section>
            </div>
        </div>
    )

}
export default StudentLogin;